/* Global variables */
/* Classes of no value
.fj-fixsize: Fix icon size unless chrome browser.
*/
.fj-util-border-box {
  box-sizing: border-box;
}
.fj-util-inline-block {
  display: inline-block;
}
/* Clear default tap background color */
a,
button,
input,
div {
  -webkit-tap-highlight-color: rgba(255, 0, 0, 0);
}
ol,
ul {
  list-style: none;
}
.fj-img-fill {
  max-width: 100%;
  height: auto;
}
.fj-form-elem {
  width: 100%;
  height: 26px;
  padding: 2px 5px 2px 2px;
  font-size: 13px;
  line-height: 1.42857143;
  color: #555;
  background-color: #fff;
  background-image: none;
  border: 1px solid #ccc;
  border-radius: 3px;
  box-shadow: inset 0 1px 1px #eeeeee;
  transition: border-color ease-in-out 0.1s, box-shadow ease-in-out 0.1s;
  box-sizing: border-box;
}
.fj-form-elem:focus {
  border-color: #c1c1c1;
  outline: 0;
  box-shadow: inset 0 1px 1px #eeeeee, 0 0 8px #c1c1c1;
}
.fj-form-elem[disabled] {
  pointer-events: none;
  cursor: not-allowed;
  box-shadow: none;
  background-color: #f0f0f0;
}
/* import all modules */
.fj-btn {
  display: inline-block;
  padding: 4px 8px;
  margin-bottom: 0;
  font-size: 13px;
  font-weight: normal;
  color: #333;
  line-height: 14px;
  text-align: center;
  white-space: nowrap;
  vertical-align: middle;
  cursor: pointer;
  user-select: none;
  background-image: none;
  border: 1px solid #ccc;
  background-color: #fff;
  border-radius: 4px;
}
.fj-btn:focus {
  outline: thin dotted;
  outline: 5px auto -webkit-focus-ring-color;
  outline-offset: -2px;
}
.fj-btn:hover,
.fj-btn:focus {
  text-decoration: none;
}
.fj-btn.fj-selected {
  background-image: none;
  outline: 0;
  box-shadow: inset 0 3px 5px rgba(0, 0, 0, 0.125);
}
.fj-btn.fj-disabled,
.fj-btn[disabled] {
  pointer-events: none;
  cursor: not-allowed;
  filter: alpha(opacity=65);
  box-shadow: none;
  opacity: 0.65;
}
.fj-btn-hover {
  background-color: #9bd1f6 !important;
}
.fj-container {
  margin-left: auto;
  margin-right: auto;
  min-width: 1025px;
  position: relative;
}
.fj-row {
  position: relative;
}
.fj-row-left,
.fj-row-right {
  position: absolute;
  top: 0;
}
.fj-row-left {
  left: 0;
}
.fj-row-right {
  right: 0;
}
